import Axios from "../api/Axios";

const axios = new Axios();

const trackUserEvent = (event) => {
    if (sessionStorage.getItem("session-id") && isValidUUID(sessionStorage.getItem("session-id"))) {
        axios.callApi("/session/trackUserEvent", {
            method: "POST",
            body: {
                event
            }
        })
    }
    else {
        sessionStorage.removeItem('session-id');
        const keyword = 'utm'; // The keyword you want to match
        const matchingParams = getParamsMatchingKeyword(keyword);
        let options = {
            method: "POST",
            body: {
                additionalDetails: matchingParams,
                event
            }
        }
        axios.callApi("/session/createNewSession", options).then((result) => {
            sessionStorage.setItem('session-id', result.data.data)
        })
    }
}

function getParamsMatchingKeyword(keyword) {
    const params = new URLSearchParams(window.location.search);
    const matchingParams = {};

    for (const [key, value] of params) {
        if (key.includes(keyword)) {
            matchingParams[key] = value;
        }
    }

    return matchingParams;
}

function isValidUUID(uuid) {
    const uuidPattern = /^[0-9a-fA-F]{8}-[0-9a-fA-F]{4}-[4][0-9a-fA-F]{3}-[89ABab][0-9a-fA-F]{3}-[0-9a-fA-F]{12}$/;
    return uuidPattern.test(uuid);
}

export default trackUserEvent